.application-process h5{
    font-family: Montserrat;
font-size: 24px;
font-weight: 700;
line-height: 34px;
text-align: left;

}
.academic-pattern::after {
    bottom: 85%;
    left: 65%;
}


.application-process .modal-cta-btn{
    cursor: pointer;
width: 292px;
height: 64px;
display:flex;
color: #FFFFFFE5;
flex-direction: column;
justify-content:center;
align-items:center;
background: #1E538F;
box-shadow: 0px 20px 24px -12px rgba(87, 137, 214, 0.4);
border-radius: 4px;
}
.application-process .modal-cta-btn p{
    font-family: Inter;
    font-size: 12px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.1em;
    text-align: center;
}
.application-process a{
color: #1E538F;
font-family: Inter;
font-size: 16px;
font-weight: 700;

}
.application-process .modal-cta-btn h6{
font-family: Inter;
font-size: 16px;
font-weight: 500;
line-height: 27px;
text-align: center;

}

