.project-card {
    background: linear-gradient(135deg, rgba(255, 255, 255, 0.4) 0%, rgba(255, 255, 255, 0.340351) 26.04%, rgba(255, 255, 255, 0.250511) 45.31%, rgba(255, 255, 255, 0.046875) 77.08%, rgba(255, 255, 255, 0) 100%);
    border: 1.5px solid rgba(0, 0, 0, 0.08);
    backdrop-filter: blur(24px);
    border-radius: 8px;
    transition: all 0.5s;
    overflow: hidden;
    height:360px;
    display: block;
}

.project-card:hover {
    transform: translateY(-10px);
    background-color: #fff;
    cursor: pointer;
}

.projct-img-container {
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
}
/* .projct-img-container .card-img-top{
   height:160px;
} */

.project-title {
    font-weight: 600;
    font-family: hind;
}

.project-description {
    font-weight: 400;
    text-align: left;

    text-justify: inter-word;
    color: rgba(9, 11, 25, 0.6);
    font-size: 14px;
    font-family: Hind;
    line-height: 138.3%;
}

.project-card-col{
    width: 20%!important;
} 

.project-card .card-body{
height:210px;
}
.project-card .card-body:hover{
    /* background-color: #fff; */
}

@media (max-width: 575px) {
    .project-card {
        padding: 5px;
        display: flex;
        height:inherit !important;
        flex-direction: row-reverse;
    }
    .project-card .card-body{
        height:inherit !important;
        }
    .project-description {
        font-size: 12px;
        font-weight: normal;
    }
    .card-body {
        padding: 0 5px;
        margin: 0px;
    }
    .card-img-top {
        width:  90px;
    }
    /* .projct-img-container .card-img-top{
        height:inherit !important;
     } */
}

@media (min-width: 992px) {
    .project-title {
        font-size: 15px;
    }
}

@media (min-width: 768px) {
   .project-card-col{
       width: 32.33333333%!important;
   } 
}
@media (max-width: 767px) {
    .project-card-col{
        width: 100%!important;
    } 
 }
@media (min-width: 992px) {
    .project-card-col{
        width: 19.4%!important;
    } 
 }

.overlaylooker {
    position: fixed;
    top: 0;
    bottom: 0;
    height: 100vh;
    width: 100vw;
    z-index: 999999999;
}