.home-section {
    /* background-image: url('../../assets//images/hero-bg.png'); */
    min-height: 100%;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    
}

.heading-home {
    color: white;
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 52px;
    line-height: 140.25%;
    position: absolute;
    top: 25%;
    max-width: 70%;
}

@media (max-width: 768px) {
    .heading-home {
        font-size: 23px;
        text-align: center;
        padding-right: 10px;
        max-width: 100%!important;
    }
}