

.contact-container {
    position: relative;
    background-size: cover!important;
    width: 100%;
    padding-top: 197px;
    background-position:  center center;
    background-repeat: no-repeat;
}

.contact-right {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.contact-right:after {
    border: 1.5px solid #ffff;
    width: 100%;
    bottom: 10%;
    position: absolute;
    top: 20%;
    z-index: 1;
    content: "";
}
.address-content{
    z-index: 2;
}
.address-content a{
   color: black
}

.contact-right .video-container img {
  border-radius: 0 12px 0 0;
}

.social-links {
    position: absolute;
    bottom: 12%;
    font-size: 22px;
}

.social-links a {
    padding: 0 15px;
    color: #090B1999;
}

@media (min-width: 1400px) {
  .contact-container .contact-info {
    Width :650px;
    }
}
@media (max-width: 575px) {
    .contact-right:after {
     display: none;
    }
    .contact-info {
        height: 164px !important;;
        width: 100%!important;
        position: inherit !important;
    }
    .social-links {
        position: inherit !important;
        width: 75%;
        font-size: 20px;
        padding-top: 20px;
    }
    .contact-info p {
        font-size: 12px!important;
    }
    .contact-container p {
        font-size: 12px!important;
    }
    .contact-info h3 {
        font-size: 14px!important;
    }
   
    .contact-container {
   
        padding-top: 50px;
   
    }
  
}

@media  (max-width: 768px) {
    .address-content p::after {
      display: none!important;
    }
}



.contact-info {
    z-index: 2;
    height:256px;
    background: rgba(255, 255, 255, 0.84);
    box-shadow: 0px 0px 40px -28px rgba(0, 0, 0, 0.28);
    backdrop-filter: blur(28px);
    Width :45%;
     border-radius: 8px;
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    position: absolute;
    bottom: 20%;
}

.contact-info h3:hover,
.contact-info p:hover,
.social-links a:hover {
    color: #1E538F !important;
}

.contact-info p {
    font-size: 20px;
    color: rgba(9, 11, 25, 0.6);
}


.contact-info h3 {
    font-size: 20px;
    font-weight: bold;
}

.contact-right button.active {
    background-color: #0077a2;
}

.address-content p::after {
    content: "";
    position: absolute;
    width: 40%;
    height: 2px;
    right: 0;
    bottom: 28%;
    background-color: rgba(0, 0, 0, 0.08);
    ;
}




@media (min-width: 1400px) {
    .address-content p::after {
        bottom: 22%;
      
    }
}
