.footer-container {
   backdrop-filter: blur(84px)!important;
/* Note: backdrop-filter has minimal browser support */
background: linear-gradient(134.03deg, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0.756784) 4.23%, rgba(255, 255, 255, 0.695124) 12.16%, rgba(255, 255, 255, 0.575849) 39.39%, rgba(255, 255, 255, 0.422193) 53.42%, rgba(255, 255, 255, 0.20818) 60.26%, rgba(255, 255, 255, 0.152271) 70.66%, rgba(255, 255, 255, 0.087343) 79.91%, rgba(255, 255, 255, 0.0333333) 89.92%, rgba(255, 255, 255, 0) 100%)!important;

   border-radius: 12px 0px 0px 12px !important;
  
}

.footer-container .links {
    height: 65px;
}

.footer-container .links a {
    text-decoration: none;
    font-family: Hind;
}

@media (max-width: 575px) {
    .footer-container .links a {
        font-size: 12px;
    }
    .footer-container .container{
padding-top: 5px!important;
padding-bottom: 5px!important;
    }
}