.innovation-container {
    background-size: cover!important;
    width: 100%;
    background-repeat: no-repeat;
    background-position: center center;

}

.tab-content {
    overflow-x: hidden;
    scrollbar-width: thin ;
   
}
.tab-content:hover{
   
    overflow-y: auto;
   
}
.tab-content::-webkit-scrollbar {
    width: 8px;  

    }
    .tab-content::-webkit-scrollbar-track {
        /* -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3); */
        border-radius: 10px;
        }
.innovation-container::after {
    bottom: 0;
    left: 12%;
}

.maxhsx {
    max-height: 100%;
}

#art-stepper::after {
    top: 55%;
}

.nav-tabs {
    border: none;
}

.nav-tabs .nav-link.active::before {
    content: '';
    position: absolute;
    display: block;
    width: 70px;
    height: 2px;
    background: rgba(25, 11, 9, 0.4);
    bottom: 0;
}

.nav-tabs .nav-link {
    margin-right: 20px!important;
}

.nav-tabs .nav-link.active {
    position: relative;
    border: none;
    -webkit-box-shadow: none;
    box-shadow: none;
    background-color: rgba(255, 0, 0, 0);
    color: #090B19!important;
}

.nav-tabs .nav-link {
    color: rgba(9, 11, 25, 0.4)!important;
}

.nav-tabs .nav-link:hover {
    color: #090B19!important;
}

.alt-header h1 {
    font-family: Montserrat;
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    line-height: 32px;
    text-align: left;
}

.alt-header p {
    font-family: Hind;
    font-size: 16px;
    text-align: left;
}

.alt-details-body h5 {
    font-family: Hind;
    color: #1E538F!important;
}

.modal-backdrop.show {
    opacity: .0;
}

.modal-bgstyle{
    background: rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(24px);
}

.inno-fellow-container {
    max-height: 100%;
}
.alt-icn img{
width: 80px!important;
}
@media(max-width:575px){
   .innovation-container .nav-tabs {
     display: block !important;
    }
    .inno-fellow-container {
        max-height: 100% !important;
    }
    .maxhsx {
        max-height: 100% !important;
    }
    .invester-title{
        width: 100% !important;
    }
}