.health-pattern::after{
    left:70%;
    top: -10%;
}
.health-pattern{
    min-height: 100vh !important;
}

.read-more-card {
    height:125px !important;
    padding:20px;
}
.health-header span {
cursor: pointer;
}
.read-more-card > div>div {
    height:200px !important;
}
.read-card-container{

    background: rgba(255, 255, 255, 0.84);
box-shadow: 0px 0px 40px -28px rgba(0, 0, 0, 0.28);
backdrop-filter: blur(28px);
}

.profile-media  img{
    height: 117px;
    /* width: 129px; */
}
.read-more-img{
    max-width: 100%;
    height: auto;
}
@media(max-width: 575px){
   
    .profile-card h5, .profile-card h5{
        font-size: 14px;
    }
   
    .read-more-card {
        height:175px !important;
      
    }
}
.profile-card{
    background:rgba(255, 0, 0, 0);


}
.health-footer .footer-text{
font-family: Montserrat;
font-size: 12px;

font-weight: 400;
line-height: 15px;
letter-spacing: 0.2em;


}
