.partner-card {
    height: 270px;
    border-radius: 8px;
    background: rgba(255, 255, 255, 0.54);
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    box-shadow: 0px 0px 40px -28px rgba(0, 0, 0, 0.28);
    backdrop-filter: blur(28px);
    border-radius: 8px;
    overflow: hidden;
    transition: 0.5s;
    cursor: pointer;
    background-color: white;
}

.partner-card:hover{
    backdrop-filter: blur(28px);
    border-radius: 8px;
    transform: scale(1.02);
}


.partner-card img {
    width: 100px;
 
}
.partner-media{
    display: flex;
    justify-content: center;
    align-items: center;
    height:100px;
}

.partner-card p {
    font-size: 20px;
    font-weight: 500;
    line-height: 28px;
    text-align: center;
    color: #090B19;
}

@media (max-width: 768px) {
    .partner-card {
        height: 240px;
        border-radius: 8px;
        padding: inherit;
    }
    .partner-card p {
        width: 100%!important;
        font-size: 18px;
    }
}

@media (max-width: 575px) {
    .partner-card {
        height: 80px;
        padding: 20px 10px;
        flex-direction: row;
    }
    .partner-card img {
        width: 50px;
        padding-top: 10px;
    }
    .partner-card p {
        font-size: 14px;
        font-weight: 500;
        font-family: hind;
        text-align: left;
        padding-left: 36px!important;
        margin-bottom: 0px !important;
        padding-bottom: 0px !important;
    }
    .partner-card:hover{
        background:none ;
       }
    
}