.team-container {
  background-size: cover !important;
  width: 100%;
  background: url("../../../assets//images/team-bg.png") center center no-repeat;
}




.team-container:after {
  top: -35%;
  left: 30%;
}

#team-stepper::after {
  top: 65% !important;
}

.team-info-left {
  width: 75%;
}

.txrBox {
  margin-top: 10px;
  margin-bottom: 10px;
  overflow-x: auto;
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none;  /* Internet Explorer 10+ */
}
.txrBox::-webkit-scrollbar { /* WebKit */
  width: 0;
  height: 0;
}

@media (max-width: 575px) {
  .txrBox{
    display: none!important;
  }
}
@media (max-width: 768px) {
  .team-card img {
    height: 164px;
  }  
  .team-info-left {
    width: 100%;
  }
}

