  }
} */


.team-slider .slick-dots li button:before {
  font-size: 12px;
  color: #1e538f;
  padding-top: 20px;
}

.desfilterbox {
    cursor: pointer;
  font-family: Montserrat;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;

  /* identical to box height */
  letter-spacing: 0.1em;

  /* Inside Auto Layout */
  flex: none;
  order: 0;
  flex-grow: 0;
  padding: 10px;
  margin: 10px;
}


.nav-pills .nav-link.active, .nav-pills .show>.nav-link {
  background: rgba(30, 83, 143, 0.04);
  color: rgba(30, 83, 143, 0.8);
  border: 2px solid rgba(30, 83, 143, 0.6);
  box-sizing: border-box;
  border-radius: 4px;
}

.nav-pills .nav-link{
  border: 2px solid rgba(0, 0, 0, 0.06);
  color: rgba(9, 11, 25, 0.32);
  box-sizing: border-box;
  margin-right: 20px!important;
  border-radius: 4px;
}
