    0px 0.56875px 1.79219px rgba(12, 20, 33, 0.013), inset 0px 2px 1px #ffffff;
}

.menuBox {
  background: linear-gradient(
    90deg,
    rgba(255, 255, 255, 0) 0%,
    rgba(255, 255, 255, 0.56) 100%
  );
  padding-right: 36px;
  padding-left: 36px;
  padding-bottom: 24px;
  padding-top: 24px;
  position: absolute;
  top: 0;
  right: 0;
  font-family: Montserrat;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  /* identical to box height */
  text-align: center;
  letter-spacing: 0.1em;
  display: flex;
  cursor: pointer;
  min-width: 172px;
  justify-content: end;
  transition: 0.5s;
}

.menuBox:hover {
  min-width: 200px;
  
}

.menuBox:hover .navLine {
  margin-bottom: 5px;
}

.svg-inline--fa.fa-w-14 {
  width: 14px !important;
}

.rounded-circle {
