.team-card {
    border-radius: 4px;
    background: #FFFFFF;
    box-shadow: 0px 12px 14px -10px rgba(0, 0, 0, 0.25);
    border-radius: 4px;
    overflow: hidden !important;
    margin: 8px;
    cursor: pointer;
 
}
.team-card img {
    height: 200px;
    object-fit: cover;

}

.backdrop-blur {
  background-color: rgba(255, 255, 255, .9);
}

/* if backdrop support: very transparent and blurred */
@supports ((-webkit-backdrop-filter: none) or (backdrop-filter: none)) {
  .backdrop-blur {
    background: linear-gradient(270deg, rgba(255, 255, 255, 0.4) 0%, rgba(234, 234, 234, 0.4) 23.96%, rgba(0, 0, 0, 0.4) 100%) !important;
    backdrop-filter: blur(17px) !important;
  }
}


.team-card img {
    transition: all 0.5s
}

.team-card .card {
    border: none;
}

.team-card:hover img {
    transform: scale(1.1);
}

.team-info {
    position: absolute;
    bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 49px;
    padding: 0px 10px;
    width: 100%;
    overflow: hidden !important;
    background: linear-gradient(270deg, rgba(255, 255, 255, 0.4) 0%, rgba(234, 234, 234, 0.4) 23.96%, rgba(0, 0, 0, 0.4) 100%);
}

.team-info h5 {
    font-size: 14px;
    font-weight: bold;
    padding: 0
}

.team-info p {
    font-size: 12px;
    padding: 0
}