.application-process h5{
    font-family: Montserrat;
font-size: 24px;
font-weight: 700;
line-height: 34px;
text-align: left;

}


.application-process .modal-cta-btn{
    cursor: pointer;
width: 292px;
height: 64px;
display:flex;
color: #FFFFFFE5;
flex-direction: column;
justify-content:center;
align-items:center;
background: #1E538F;
box-shadow: 0px 20px 24px -12px rgba(87, 137, 214, 0.4);
border-radius: 4px;
}
.application-process .modal-cta-btn p{
    font-family: Inter;
    font-size: 12px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.1em;
    text-align: center;
}
.application-process a{
color: #1E538F;
font-family: Inter;
font-size: 16px;
font-weight: 700;

}
.application-process .modal-cta-btn h6{
font-family: Inter;
font-size: 16px;
font-weight: 500;
line-height: 27px;
text-align: center;

}

.tm-card{
    justify-content: end;
    flex-direction:column;
    padding:24px;
    height: 206px;
    width: 367px;
    text-align: left;
    border-radius: 8px;
    background: linear-gradient(134.03deg, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0.756784) 4.23%, rgba(255, 255, 255, 0.695124) 6.77%, rgba(255, 255, 255, 0.575849) 21.88%, rgba(255, 255, 255, 0.422193) 31.25%, rgba(255, 255, 255, 0.20818) 50.52%, rgba(255, 255, 255, 0.152271) 70.66%, rgba(255, 255, 255, 0.087343) 79.91%, rgba(255, 255, 255, 0.0333333) 89.92%, rgba(255, 255, 255, 0) 100%);
   backdrop-filter: blur(44px);
/* Note: backdrop-filter has minimal browser support */
}
@supports ((-webkit-backdrop-filter: none) or (backdrop-filter: none)) {
    .tm-card {
      -webkit-backdrop-filter: blur(44px);
      background: linear-gradient(134.03deg, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0.756784) 4.23%, rgba(255, 255, 255, 0.695124) 6.77%, rgba(255, 255, 255, 0.575849) 21.88%, rgba(255, 255, 255, 0.422193) 31.25%, rgba(255, 255, 255, 0.20818) 50.52%, rgba(255, 255, 255, 0.152271) 70.66%, rgba(255, 255, 255, 0.087343) 79.91%, rgba(255, 255, 255, 0.0333333) 89.92%, rgba(255, 255, 255, 0) 100%);
      backdrop-filter: blur(44px) !important;  
    }
  }
  .tm-card h5{
    font-family: Montserrat;
    font-weight: bold;
    font-size: 16px;
  
    color: #090B19;
  }
  .tm-card p{
   
font-family: Hind;
font-size: 14px;
line-height: 140.25%;


color: #090B19;
  }
  



