.section-common-info{
    position: relative!important;
    background-size: cover!important;
    width: 100%;
    z-index: 49 !important;
    background-color: #fff!important;
    background: url('../../../assets//images/news-bg.png') center center no-repeat;
}

.team-info-section h5{
    cursor: pointer;
}
.team-info-section:after {
    border: 2px solid #ffff;
    width: 100%;
    top: 10%;
    bottom: 10%;
    left: 40%;
    position: absolute;
    z-index: -1;
    content: "";
}
