.tm-modal .modal-body, .tm-modal .modal-header {
    padding: 0px!important;
}


.team-member-info {
    background: linear-gradient(134.03deg, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0.756784) 4.23%, rgba(255, 255, 255, 0.695124) 12.16%, rgba(255, 255, 255, 0.575849) 39.39%, rgba(255, 255, 255, 0.422193) 53.42%, rgba(255, 255, 255, 0.20818) 60.26%, rgba(255, 255, 255, 0.152271) 70.66%, rgba(255, 255, 255, 0.087343) 79.91%, rgba(255, 255, 255, 0.0333333) 89.92%, rgba(255, 255, 255, 0) 100%);
    backdrop-filter: blur(44px);
    border-radius: 16px 0px 0px 16px;
    padding: 64px;
}
.team-member-info img{
   height:500px;
   
   padding-top: 25px!important;
}
.team-media {
    position: absolute;
    top: 0px !important;
    bottom: 0px!important;
   
}

.team-media  img{
  height: 626px !important;
   
}

.linkedin-btn {
    background-color: #1E538F;
    color: #fff;
    align-items: center;
    font-family: Inter;
    font-weight: 500;
    width: 260px;
    height: 55px;
    font-size: 16px;
    line-height: 148.3%;
    cursor: pointer;
    border-radius: 4px;
}

.linkedin-btn:hover {
    background-color: #1E538F;
    color: #fff;
}

.linkedin-btn span {
    border-right: 2px solid rgba(0, 0, 0, 0.12);
    padding: 16px;
   
}

.team-media {
    justify-content: end;
    display: flex;
    align-items: end;
}

@media (max-width: 768px) {
    .team-img:after {
        display: none
    }
    .team-media {
        justify-content: center;
        align-content: center;
        display: flex;
    }
    .team-member-info {
        padding: 10px;
    }
    .team-media {
        position: inherit;
    }
    .linkedin-btn {
        width: 100% !important;

    }
    .team-media  img{
        height:300px;
        padding-top: 5px;
         
      }
      .modal  .modal-dialog{
        overflow-y: auto!important;
      }
      .section-common-info {
        padding: 10px 0px!important;
          /* height: 100vh !important; */
          /* overflow-y: auto!important; */
      }
}
@media (max-width: 575px) {

    .section-common-info {
        align-items: flex-start;
        padding-right: 12px!important;
        padding-left: 12px!important;
    }
    .team-info-section:after {
       display: none ;
    }
 
}
