.news-container {
    background-size: cover!important;
    width: 100%;
    background-repeat: no-repeat;
    background-position: center center;
  
}

#news-stepper::after {
    top: 70%;
}

.news-media {
    max-height: 302px;
    max-width: 658px;
    border-radius: 12px;
}

.max28 {
    max-width: 28px !important;
}

.news-slider {
    min-height: 163px;
}

.news-slider .slick-slider {
    min-height: 163px;
}
.news-slider .slick-slider .slick-list {
    min-height: 163px;
}

.news-story-card {
    padding: 28px !important;
    max-height: 285px;
    width: 100%;
    background: linear-gradient(134.03deg, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0.756784) 4.23%, rgba(255, 255, 255, 0.695124) 12.16%, rgba(255, 255, 255, 0.575849) 39.39%, rgba(255, 255, 255, 0.422193) 53.42%, rgba(255, 255, 255, 0.20818) 60.26%, rgba(255, 255, 255, 0.152271) 70.66%, rgba(255, 255, 255, 0.087343) 79.91%, rgba(255, 255, 255, 0.0333333) 89.92%, rgba(255, 255, 255, 0) 100%);
    backdrop-filter: blur(44px);
    border-radius: 12px 0px 0px 12px;
    overflow: hidden;
    transform: translate(10%, -40%)
}

.news-story-card h3 {
    font-weight: bold;
    font-size: 20px;
    line-height: 160.3%;
}

.news-story-card p {
    color: rgba(9, 11, 25, 0.7);
}

.slider-card {
   min-height: 149px;
    max-width: 367px;
    border-radius: 8px;
    background: rgba(255, 255, 255, 0.94);
    box-shadow: 0px 0px 40px -28px rgba(0, 0, 0, 0.28);
    backdrop-filter: blur(14px);
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    justify-content: center;
    padding: 24px;
    transform: translateY(10px);
    transition: 0.5s;
}

.slider-card:hover {
    box-shadow: 0px 0px 40px -28px rgba(0, 0, 0, 0.28);
    transform: translateY(0px);
}

.slick-arrow {
    display: none !important;
}

.news-logo img {
    border-radius: 50%!important;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
        width: 28px;
       height: 28px;  
    
}

.news-details-titile {
    font-family: Hind;
    font-size: 20px;
    font-weight: 700;
    padding-left: 12px;
}

@media (max-width:768px) {
    .news-story-card {
        padding: 15px!important;
        transform: translate(10%, -20%)
    }
    .news-story-card p {
        font-size: 11px;
        padding-right: 5px;
    }
    .news-story-card h3 {
        font-size: 13px;
    }
    .news-story-card>div {
        width: 95% !important;
        ;
    }
   
}

.news-slider .slick-dots li button:before {
    font-size: 12px;
    padding-top: 20px;
    color: #1E538F;
}

