.innovation-card {
    padding: 15px 40px 15px 15px;
    border-radius: 8px;
    display: flex;
    justify-content: start;
    flex-direction: row;
    align-items: center;
    background: linear-gradient(135deg, rgba(255, 255, 255, 0.9) 0%, rgba(255, 255, 255, 0.084375) 52.08%, rgba(255, 255, 255, 0) 100%);
    border: 1.5px solid #eceaea;
    box-sizing: border-box;
    backdrop-filter: blur(24px);
    cursor: pointer;
  
}
 .innovation-media {
    padding: 28px;
}

@media (max-width: 575px) {
    .innovation-card {
        padding: 10px;
        flex-direction: column;
    }
    .innovation-media {
        padding: 18px;
    }
    .innovation-body p:first-child {
        font-size: 14px!important;
    }
    .innovation-body p {
        font-size: 12px;
        text-align: center;
    }
}