.career-container {
    background-size: cover!important;
    width: 100%;
    z-index: 1060;
    background-repeat: no-repeat;
    background-position: center center;
}

.career-container:after {
    top: -350px;
    left: 60%;
}
.active-sldr-btn{
    background:transparent;
    border:2px solid black!important;
    border-radius: 50%!important;
    color: black;
    
    }
    .disable-sldr-btn{
        background:transparent;
        border:2px solid #777 !important;
        border-radius: 50%!important;
        color: #777;
        
        }


.career-card {
    padding: 24px 0 4px 24px;
    max-height: 149px;
    cursor: pointer;
    border-radius: 8px;
    border: 1.5px solid #FFFFFF;
    background: linear-gradient(134.03deg, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0.756784) 4.23%, rgba(255, 255, 255, 0.695124) 6.77%, rgba(255, 255, 255, 0.575849) 21.88%, rgba(255, 255, 255, 0.422193) 31.25%, rgba(255, 255, 255, 0.20818) 50.52%, rgba(255, 255, 255, 0.152271) 70.66%, rgba(255, 255, 255, 0.087343) 79.91%, rgba(255, 255, 255, 0.0333333) 89.92%, rgba(255, 255, 255, 0) 100%);
    transition: 0.5s;
    overflow: hidden;
}

/* .modal-dialog .modal-content {
    background-color: rgba(255, 255, 255, .9);
    overflow-y: scroll;
  }
   */
  /* if backdrop support: very transparent and blurred */
  @supports ((-webkit-backdrop-filter: none) or (backdrop-filter: none)) {
    .backdrop-blur {
      -webkit-backdrop-filter: blur(64px);
      background: linear-gradient(134.03deg, rgba(255, 255, 255, 0.9) 0%, rgba(255, 255, 255, 0.99) 4.23%, rgba(255, 255, 255, 0.995124) 12.16%, rgba(255, 255, 255, 0.575849) 39.39%, rgba(255, 255, 255, 0.422193) 53.42%, rgba(255, 255, 255, 0.20818) 60.26%, rgba(255, 255, 255, 0.152271) 70.66%, rgba(255, 255, 255, 0.087343) 79.91%, rgba(255, 255, 255, 0.0333333) 89.92%, rgba(131, 51, 51, 0) 100%);
      box-shadow: 0px 0px 40px -28px rgba(0, 0, 0, 0.28);
      backdrop-filter: blur(64px) !important;  
    }
  }
  

.career-card p {
    font-weight: 500;
}

.career-card:hover {
    transform: translateY(-10px);
    background: linear-gradient(172.61deg, #FFFFFF 0%, rgba(255, 255, 255, 0.94598) 10.78%, rgba(255, 255, 255, 0.868905) 17.27%, rgba(255, 255, 255, 0.719811) 55.79%, rgba(255, 255, 255, 0.527741) 79.71%, rgba(255, 255, 255, 0.260225) 128.86%, rgba(255, 255, 255, 0.190338) 180.22%, rgba(255, 255, 255, 0.109179) 203.83%, rgba(255, 255, 255, 0.0416667) 229.35%, rgba(255, 255, 255, 0) 255.06%);
}
.team-media img{
    width: 638px;
    height: 714px;
}
.career-modal .modal-dialog{
    max-width: 100vw!important; 

  }
   
@media (max-width: 575px) {

   

    .career-card p {
        font-size: 12px;
    }
    .apply-btn {
        padding: 10px 0px!important;
        width: 100%;
    }
    
    .career-modal .modal-dialog{
     
        min-width: 90vw!important; 
       
    
      }
    
    /* .modal-body {
       height: 100vh !important;
       overflow-y: auto!important;
       z-index:2020 !important;
    } */
    .modal-90w p{
   
        font-size: 12px; 
    }
    .modal-90w ul li{
        font-size: 12px; 
    }
    .modal {
        padding-bottom: 20px;
        top: 31px !important;
        border-radius: 16px !important;     
    }
