.career-slider .slick-dots li {
    padding: 10px 20px;
}

.career-slider .slick-dots li button:before {
    content: " "!important;
    top: 20px;
    width: 40px;
    height: 4px;
    padding: 0px 10px;
    background-color: #a19c9c;
}

.modal-dialog .modal-content {
    border-radius: 8px 0px 0px 8px;
}

.apply-btn {
    padding: 12px 120px;
    /* padding: 15px 120px; */
    font-size: 15px;
     letter-spacing: 1px;
   background: rgba(30, 83, 143, 0.04);
    border: 2px solid #1E538F;
    box-sizing: border-box;
    border-radius: 4px;
}
.btn-bg{
 color: #fff;
 font-weight: bold;
 background:  #1e538f !important;
} 

.modal-90w {
    width: 77vw;
    max-width: 77vw;
    top: 30px;
}

.career-modal{
    overflow: hidden!important;
}

.career-modal .modal-header {
    padding: 16px 16px 0px 16px ;
    border-bottom: none;

}
.career-modal .modal-body::-webkit-scrollbar {
    width: 8px;  

    }
    .career-modal .modal-body::-webkit-scrollbar-track {
        -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
        border-radius: 10px;
        }

 .career-modal .modal-body {
    padding: 0px 16px 16px 16px ;
    max-height: 80vh !important;
    overflow-y:auto;
}

.modal-header .btn-close {
    border-radius: 50%;
    border: 1px solid rgb(98, 98, 98);
    font-size: 8px;
    margin: 0px !important;
}


.modal-80h {
    max-width: 80vh !important;
    overflow-y: auto;
    overflow-x: hidden;
}
.career-body-text{
    padding-top:30px
}

@media(max-width: 575px){
   
  
    
    .career-modal  .modal-header .btn-close {
      margin-top: -20px;
    }
    
}