.card-media {
    width: 56px;
    height: 56px;
    background: rgba(255, 255, 255, 0.6);
    overflow: hidden;
    border-radius: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.card-media img {
    max-height: 40px;
}

.textify {
    padding-left: 16px !important;
}

.aboutCard:nth-child(1) .card-container {
    border-top-left-radius: 12px !important;
    border-bottom-left-radius: 12px !important;
    border-top-right-radius: 0px !important;
    border-bottom-right-radius: 0px !important;    
}

.aboutCard:nth-child(2) .card-container {
    border-top-left-radius: 0px !important;
    border-bottom-left-radius: 0px !important;
    border-top-right-radius: 0px !important;
    border-bottom-right-radius: 0px !important;    
}

.aboutCard:nth-child(3) .card-container {
    border-top-left-radius: 0px !important;
    border-bottom-left-radius: 0px !important;
    border-top-right-radius: 12px !important;
    border-bottom-right-radius: 12px !important;    
}

.card-container {
    height: 199px;
    border: 1.5px solid #FFFFFF;
    box-sizing: border-box;
    border-radius: 12px;
    padding: 36px;
}

.card-container h1 {
    font-family: Montserrat;
    font-weight: bold;
    font-size: 20px;
    padding-top: 19px;
    line-height: 140.25%;
    color: #2E2E2E;
}

.card-container p {
    font-family: Hind;
    font-size: 16px;
    color: #090B19;
}

@media (max-width: 768px) {
    .card-container p {
        font-size: 12px;
        width: 100%;
    }
    .card-container h1 {
        font-size: 14px;
        width: 100%!important;
    }
    .card-container {
        padding: 23px 10px;
        height: 229px;
    }
}

@media (max-width: 575px) {
    .card-container {
        display: flex;
        padding: inherit;
        align-items: center;
        height: 129px;
    }
    .card-container p {
        font-size: 12px;
    }
    .card-container h1 {
        font-size: 14px;
        font-family: Inter;
        font-weight: 700;
        line-height: 20px;
    }
    .card-text {
        padding-left: 15px;
    }
    .card-media {
        width: 50px;
        height: 47px;
        padding: 10px;
      
    }
    .aboutCard:nth-child(1) .card-container {
        border-radius: 12px !important;
    }
    .aboutCard:nth-child(2) .card-container {
        border-radius: 12px !important;  
    }
    
    .aboutCard:nth-child(3) .card-container {
        border-radius: 12px !important;     
    }
}