.project-container .body-text {
    max-width: 65%;
}

#project-stepper::after {
    top: 48%;
}

.project-container {
    background-size: cover!important;
    width: 100%;
    background-position:  center center ;
    background-repeat: no-repeat;
}

.project-modal .modal-dialog{
    max-width: 60vw; 
  }
  @media(min-width:1400px){
    .project-modal .modal-dialog{
        max-width: 800px !important; 
      }   
  }
  .visit-btn{
    padding: 10px 50px;
    border: 2px solid #090b19;
    border-radius: 4px!important;
    letter-spacing:2px

   }
   .visit-btn:hover{
   background-color: #1e538f;
   color: #fff;

   }

@media (max-width: 575px) {
    .project-container p {
        max-width: 100%!important;
    }
    .project-modal .modal-dialog{
        max-width: 95vw; 
      }
      .tm-modal-media img{
        max-height:inherit;
        width: 100%!important;
        padding-bottom: 15px;
        
    }
    
}

.project-container {
    overflow: hidden;
}

.project-container::after {
    left: 5%;
    top: -40%;
}

.project-modal .modal-header .btn-close {
    margin-right: 20px!important;
    margin-top: 15px!important;
}
.project-modal .modal-header  {
  padding: 0px 0px 0px 0px;
}
.project-modal .modal-body  {
    padding-top: 0px ;
  }
  

.tm-modal-media img{
    max-height:200px;
    
}
