.fund-card {
    height: 129px;
    border-radius: 8px;
    display: flex;
    justify-content: start;
    align-items: center;
    background: linear-gradient(135deg, rgba(255, 255, 255, 0.9) 0%, rgba(255, 255, 255, 0.084375) 52.08%, rgba(255, 255, 255, 0) 100%);
    border: 1.5px solid rgba(0, 0, 0, 0.04);
    box-sizing: border-box;
    /* backdrop-filter: blur(24px); */
    transition: 0.5s;
    border-radius: 8px;
    cursor: pointer;
}
.fund-card:hover{
    background: rgba(255, 255, 255, 0.84);
    /* opacity: 0.6; */
    /* box-shadow: 0px 0px 40px -28px #1E538F; */
    /* backdrop-filter: blur(28px); */
    /* border-radius: 8px; */
    transform: scale(1.02);
}


.fund-media {
    padding: 38px;
}

@media (max-width: 575px) {
    .fund-media img {
        width: 40px !important;
    }
    .fund-media {
        padding: inherit;
    }
    .fund-card {
        height: 80px;
        padding: 20px 10px;
    }
    .fund-card p {
        text-align: left!important;
        padding-right: 2px;
        font-size: 14px;
        color: #090B19;
        padding-left: 25px!important;
    }
    .fund-card:hover{
        background: none ;
       }
}