@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
.App {
  font-family: 'Hind', sans-serif;
  font-family: 'Inter', sans-serif;
  font-family: 'Montserrat', sans-serif;
}

.logoBox {
  background-color: white;
  border-bottom-left-radius: 16px;
  border-bottom-right-radius: 16px;
  padding: 16px;
  position: absolute;
  top: 0;
  z-index: 1050;
  box-shadow: 0px 13px 31px rgba(12, 20, 33, 0.04),
    0px 9.45547px 20.8947px rgba(12, 20, 33, 0.032375),
    0px 6.58125px 13.5141px rgba(12, 20, 33, 0.027),
    0px 4.31641px 8.38574px rgba(12, 20, 33, 0.023125),
    0px 2.6px 5.0375px rgba(12, 20, 33, 0.02),
    0px 1.37109px 2.99707px rgba(12, 20, 33, 0.016875),
    0px 0.56875px 1.79219px rgba(12, 20, 33, 0.013), inset 0px 2px 1px #ffffff;
}

.menuBox {
  background: linear-gradient(
    90deg,
    rgba(255, 255, 255, 0) 0%,
    rgba(255, 255, 255, 0.56) 100%
  );
  padding-right: 36px;
  padding-left: 36px;
  padding-bottom: 24px;
  padding-top: 24px;
  position: absolute;
  top: 0;
  right: 0;
  font-family: Montserrat;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  /* identical to box height */
  text-align: center;
  letter-spacing: 0.1em;
  display: flex;
  cursor: pointer;
  min-width: 172px;
  justify-content: end;
  transition: 0.5s;
}

.menuBox:hover {
  min-width: 200px;
  
}

.menuBox:hover .navLine {
  margin-bottom: 5px;
}

.svg-inline--fa.fa-w-14 {
  width: 14px !important;
}

.rounded-circle {
  height: 26px !important;
  width: 26px !important;
  display: flex;
  justify-content: center;
  align-items: center;
}

.closeBox {
  padding-right: 36px;
  padding-left: 36px;
  padding-bottom: 24px;
  padding-top: 24px;
  position: absolute;
  top: 0;
  right: 0;
  font-family: Montserrat;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  /* identical to box height */
  text-align: center;
  letter-spacing: 0.1em;
  display: flex;
  cursor: pointer;
}

.navCloseBox {
  height: 20px;
  width: 24px;
  font-family: Montserrat;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  
  /* identical to box height */
  text-align: center;
  letter-spacing: 0.1em;
  
  color: rgba(46, 46, 46, 0.8);
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.navBox {
  height: 20px;
  width: 24px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin-right: 16px;
}

.navLine {
  width: 24px;
  height: 0px;
  margin-bottom: 4px;
  transition: 0.5s;
}

.text-blue {
  color: rgba(46, 46, 46, 0.8);
}
.colaps-text{
  color: rgba(46, 46, 46, 0.8)!important;
}
.colaps-text:hover{
  color:  #1e538f !important;
}

.navLine-white {
  border: 2px solid #ffffff;
}

.navLine-black {
  border: 2px solid rgba(46, 46, 46, 0.8);
}

.mobile-toggle {
  margin-top:4px;
  border: 1px solid #1e538f;
}

.ScrollDown {
  position: absolute;
  bottom: 20px;
  width: 100%;
  display: flex;
  justify-content: center;
}

/* this class making mobile responsibe problem */



.down-icon {
  cursor: pointer;
  font-size: 26px;
  transition: 0.5s;
}

.down-icon:hover {
  cursor: pointer;
  transform: scale(1.2);
}
