.FullView {
  position: fixed;
  overflow: hidden;
  height: 0vh;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1040;
  background-color: white;
  transition: 0.5s;
  z-index: 70;
}
.FullView .contentDiv .nav-link {
  color: rgba(46, 46, 46, 0.8);
  display: block;
 
}
.FullView .contentDiv .outlineless-btn:focus  {
  outline: none;
  box-shadow: none;
 
}
/* .FullView .contentDiv .nav-right-arrow{
  
} */
.FullView .contentDiv .nav-link:hover ,.FullView .contentDiv .nav-right-arrow:hover{

   color: #1e538f !important;
  

}

.contentDiv {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  /* top: -100px; */
  z-index: 8;
}

.contentDiv ul {

  list-style-type: none;
  text-align: center;
}


.contentDiv ul li {
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  line-height: 140.25%;
  text-align: center;
  padding-top: 5px;
  padding-bottom: 5px;
  
  cursor: pointer;
}
.contentDiv ul li a{
  color: rgba(46, 46, 46, 0.8);
}

 
.contentDiv ul li a:hover {
  color: #1e538f;
}

.contentDiv .menuFooter {
  position: fixed;
  width: 100vw;
  bottom: 32px;
  text-align: center;
  font-size: 22px;
}
.contentDiv .menuFooter a{
text-decoration: none!important; 
color: #777;

}
.contentDiv .menuFooter .top-socila-link{
 width: 15%;
  
  }
  .contentDiv .menuFooter .top-socila-link a:hover{
    color: #1e538f;
    transition: 0.5s;
     
     }
   


.scoIcon {
  padding: 16px;
  font-size: 18px;
  
}

section {
  min-height: 900px;
  padding-top: 120px;
  padding-bottom: 20px;
  overflow: hidden;
}

.container {
  padding-left: 50px;
}



.navbar-light {
  padding: 0px !important;
}

.stepperBar {
    position: absolute;
    display: block;
    width: 2px;
    height: 700px;
    background: rgba(0, 0, 0, 0.1);
    left: 5%;
    top: 15%;
    z-index: 100;
}

.stepperPos {
    position: absolute;
    display: block;
    width: 5px;
    height: 100px;
    background: #ffffff;
    z-index: 600;
    left: calc(5% - 2px);  
    top: 15%;
    cursor: pointer;
    z-index: 101;
  }



ol, ul {
  padding-left: 0rem !important;
}

.iScrollVerticalScrollbar.iScrollLoneScrollbar {
  display: none !important;
}

