.section-title {
  font-family: Montserrat;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 0.2em;
  color: #090b19;
  text-transform: uppercase;
}

.primary-text {
  color: #090b19;
}

.secondary-text {
  color: #1e538f;
}

.muted-test {
  color: rgba(46, 46, 46, 0.5);
  font-weight: 500;
  font-size: 14px;
}

.primary-fnt {
  font-family: Hind;
}

.subtitle {
  font-family: montserrat;
  font-weight: bold;
  font-size: 39px;
  line-height: 140.25%;
  padding: 15px 0;
  color: #1e538f;
}

.main-btn {
  padding: 8px 28px;
  color: #1e538f;
  background: rgba(255, 255, 255, 0.5);
  border: 1.5px solid #ffffff;
  box-sizing: border-box;
  backdrop-filter: blur(14px);
  border-radius: 8px;
  font-size: 14px;
  text-align: center;
  letter-spacing: 0.2em;
  text-transform: uppercase;
}

.body-text {
    font-family: Hind;
    font-size: 16px;
    font-weight: 400;
    line-height: 148.3%;
}
p{
  font-family: Hind;
    font-size: 16px;
    font-weight: 400;
    line-height: 148.3%; 
}
ul li{
  font-family: Hind;
  font-size: 16px;
  font-weight: 400;
  line-height: 148.3%;
}

.pattern {
  position: relative;
}

.pattern::after {
    height: 500px;
    content: ' ';
    position: absolute;
    width: 100%;
    background-repeat: no-repeat;
    background-image: url('./assets/images/Pattern\ 1.png');
    z-index: -50;
}
.pinter-cursor {
  cursor: pointer!important;
  display: inline-block;
}
