.academic-card {
    height: 275px;
    padding: 10px 20px 10px 30px;
    cursor: pointer;
    width: 100% !important;
    border: 1.5px solid white;
    background: linear-gradient(134.03deg, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0.756784) 4.23%, rgba(255, 255, 255, 0.695124) 12.16%, rgba(255, 255, 255, 0.575849) 39.39%, rgba(255, 255, 255, 0.422193) 53.42%, rgba(255, 255, 255, 0.20818) 60.26%, rgba(255, 255, 255, 0.152271) 70.66%, rgba(255, 255, 255, 0.087343) 79.91%, rgba(255, 255, 255, 0.0333333) 89.92%, rgba(255, 255, 255, 0) 100%);
    backdrop-filter: blur(44px);
    border-radius: 16px;
}

.academic-card h1 {
    font-family: Montserrat;
    color: #1E538F;
    font-size: 37px;
    font-weight: 700;
    padding-bottom: 10px;
    line-height: 56px;
    text-align: left;
}

.academic-card p {
    font-family: Inter;
    font-size: 16px;
    font-weight: 400;
    line-height: 25px;
    color: #090B19B2;
    text-align: left;
}

@media (max-width: 575px) {
    .academic-card h1 {
        font-size: 24px;
        line-height: inherit;
    }
    .academic-card {
        height: inherit !important;
        padding: 14px;
    }
    .academic-card p {
        line-height: inherit;
        font-size: 14px;
    }
}