

.health-text{
    font-family: Hind;
   font-size: 12px;
   line-height: 138.3%;
   color: rgba(9, 11, 25, 0.6);
}
.health-card{
    background: #F2F6FC;
   cursor: pointer;
   border: 1px solid rgba(214, 16, 16, 0);
   box-shadow: 0px 0px 40px -28px rgba(0, 0, 0, 0.28);
   ackdrop-filter: blur(28px);
  

  border-radius: 8px;   
}
.health-card img {
  height: 342px;
  border-radius: 10px;
  /* width: 342px; */
}
.health-header .health-fst-text{
  font-size:30.5px;
  color: #1e538f;

}
.health-description p{  
  font-size:14.5px;
  margin-bottom: 8px;
 
}

.health-contact{
    width: 560px;
   /* height: 200px; */
   text-align:left;
   display:flex;
   flex-direction: column;
   justify-content:space-around;
  
  
}
.health-contact h2{
font-family: Hind;
font-weight: bold;
font-size: 20px;
line-height: 140.25%;
color: #090B19;


}
  .health-contact p{
    font-family: Montserrat;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0.2em;
    color: #090b19;
  }
.health-contact h3{
     font-family: Hind;
     font-size: 20px;
    line-height: 140.25%;
    color: rgba(9, 11, 25, 0.6);
}
.health-contact h5{
    font-family: Montserrat;
font-size: 20px;
font-style: normal;
font-weight: 700;
display: inline-block;
line-height: 22px;
letter-spacing: 0.1em;
color: rgba(9, 11, 25, 0.6);
text-align: left;

}


  




  .health-footer a{
      color:#5C636A !important;
      padding-left: 15px;
  }
  @media (max-width:575px){
    .health-header h3 , .health-header p {
      width: 100%!important;
    }

  
  .health-footer p{
    font-size :12px !important;;
  }
  .health-contact{
    width: inherit !important;
    height: inherit !important;
  }
  .health-contact h3 , .health-contact h5{
    font-size: 16px;

 }

.health-header, .health-header >div{
  width: 100% !important;
}

