
.academic-info-Card .academic-card-container {
    /* min-height: 610px; */
    /* width: 366px; */
   border-radius: 8px;
   padding: 24px;
    background: #FFFFFF;
}
.academic-info-Card .card-media {
    width: 56px;
    height: 56px;
    background: #E3EFF6;
    overflow: hidden;
    border-radius: 50px;
   
  
}

 .academic-info-Card .academic-card-container h1 {
    font-family: Montserrat;
    font-size: 16px;
    padding: 12px 0px!important;
    font-weight: 700;
    line-height: 22px;
    text-align: left;   
    color: #2E2E2E;
}

