.card-text .subtitle {
    font-family: Inter;
    font-style: normal;
    font-weight: bolder!important;

    line-height: 166.3%;
    /* or 23px */
    
    color: #090B19;
    
}

@media (max-width: 768px) {
   .academic-info-Card .academic-card-container p {
        font-size: 12px;
        width: 100%;
    }
  .academic-info-Card  .academic-card-container h1 {
        font-size: 14px;
        width: 100%!important;
    }
   .academic-info-Card .academic-card-container {
        padding: 23px 10px;
        /* min-height: 129px !important;; */
    }
}

.academic-card-container {
    height: 400px;
}

.academic-card-container .card-text {
    padding: 8px !important;
}

@media (max-width: 575px) {
   .academic-info-Card .academic-card-container {
        display: flex;
        padding: inherit;
        align-items: center;
    }
    .academic-card-container p {
        font-size: 12px;
    }
    .academic-card-container h1 {
        font-size: 14px;
        font-family: Inter;
        font-weight: 700;
        line-height: 20px;
    }
    .card-text {
        padding-left: 0px;
    }
   .academic-info-Card .card-media {
        width: 50px;
        height: 40px;
        padding: 10px;
      
    }
    .academic-card-container {
        height: auto;
    }
}