
.application-process .qualified-team{
    cursor: pointer;
    width: 292px;
    height: 64px;
    display:flex;
    color: #FFFFFFE5;
    flex-direction: column;
    justify-content:center;
    align-items:center;
    border:2px solid #1E538F;
    color: #1E538F;
    box-shadow: 0px 20px 24px -12px rgba(87, 137, 214, 0.4);
    border-radius: 4px;
    }
   

    .contapp {
        padding-top: 0px !important;
      padding-left: 0px !important;
    }
  
    .application-process .qualified-team h6{
    font-family: Inter;
    font-size: 16px;
    font-weight: 500;
    line-height: 27px;
    text-align: center;
    
    }

    .participation-footer h5{
        line-height: 25px;
        font-family: Montserrat;
    font-size: 24px;
   font-weight: 700;
   line-height: 34px;
  
    }

    .participation-footer a{
      color:#1E538F;
    }

    .participation-footer p{
        font-family: Inter;
    color: #090B19;

    }

    .robot-challenge-title{
        font-family: Montserrat;
   font-style: normal;
   font-weight: bold;
   font-size: 24px;
   line-height: 140.25%;

   color: #090B19;
    }
    .robot-challenge-des{
        font-family: Inter; 
        font-size: 16px;
        line-height: 158.3%;

        
        color: #090B19;
    }

    .scroing p{
        font-family: Inter;
font-style: normal;
font-weight: normal;
font-size: 16px;
line-height: 158.3%;
color: #090B19;

    }
    /* .robo-nav-container .nab-line{
        display: block!important;
    } */

    .scroing ol li, .addition-info ol li{
        font-family: Inter;
        font-size: 16px;
        line-height: 198.3%;
        color: #090B19;
    }

    .proposal-cotaint> p{
        font-family: Inter;
     color: #090B19;

    }
    .proposal-list h5{
        font-family: Inter;
        font-weight: 500;
    }
    .proposal-list p{
        font-family: Inter;
        font-weight: 400;
    }
    .addition-info h5{
     font-family: Inter;
    font-weight: 600;
    font-size: 16px;
    color: #090B19;

    }

    @media(max-width: 575px){
        .application-process .qualified-team h6{
          
            font-size: 16px;

            }
            .application-process >div{
                width: 100%!important;
            }
    }

    .stage-circle{
        width: 20px;
        height: 20px;
        border-radius:50%;
   
      
        display: inline-block;
      border: 2px solid #000000;


    }
    .stage-section p{
        border-left: 2px dotted #000000;
        margin-left: 8px;
        padding-left:10px;
    }

    